const createWriteOffCode = (writeOffType) => {
    let writeOffCode;
    switch (writeOffType) {
      case '':
        writeOffCode = 401;
        break;
      case '':
        writeOffCode = 402;
        break;
      default:
        writeOffCode = 0;
    }
    return writeOffCode
}

export default createWriteOffCode;
